  align-items: center;
}
.information__input-name,
.information__input-weight,
.information__input-age {
  height: 44px;
  margin-left: 10px;
  flex-grow: 1;
}
.information__input-name[type="text"],
.information__input-weight[type="text"],
.information__input-age[type="text"] {
  padding-left: 15px;
}
.information__input-name[type="number"],
.information__input-weight[type="number"],
.information__input-age[type="number"] {
  padding-left: 15px;
}
@media (min-width: 768px) {
  .information__input-name,
  .information__input-weight,
  .information__input-age {
    font-size: 20px;
    line-height: 30px;
    height: 52px;
  }
}
.information__input-age {
  margin-left: 9px;
}
.program-selection {
  margin: 0;
  padding: 0;
  border: none;
}
.program-selection::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  box-shadow: 0 1px 0 0 #d9d9d9, 0 155px 0 0 #d9d9d9;
  width: 100%;
  height: 1px;
}
@media (min-width: 768px) {
  .program-selection {
    border: none;
  }
  .program-selection::before {
    display: none;
  }
}
@media (min-width: 768px) {
  .program-selection__wrapper {
    padding: 71px 40px;
  }
}
@media (min-width: 1400px) {
  .program-selection__wrapper {
    padding: 0;
    padding-right: 40px;
    margin-left: 80px;
  }
}
.program-selection__list {
  padding: 0;
  margin: 0;
  list-style: none;
  padding-top: 22px;
  padding-bottom: 20px;
}
@media (min-width: 768px) {
  .program-selection__list {
    border: 1px solid #d9d9d9;
    padding-top: 34px;
    padding-bottom: 34px;
    padding-left: 28px;
  }
}
.program-selection__item {
  padding-bottom: 20px;
}
.program-selection__item:last-child {
  padding-bottom: 0;
}
.program-selection__caption {
  position: relative;
  padding-left: 45px;
  color: #222222;
}
.program-selection__caption--disabled {
  opacity: 0.3;
}
.program-selection__caption--disabled .program-selection__radio {
  box-shadow: 0 0 0 1px #bababa;
}
@media (min-width: 768px) {
  .program-selection__caption {
    padding-left: 60px;
  }
}
.program-selection__input {
  appearance: none;
  color: #222222;
}
.program-selection__radio {
  position: absolute;
  width: 14px;
  height: 14px;
  margin-left: -45px;
  margin-top: -2px;
  background-color: #FFFFFF;
  box-shadow: 0 0 0 1px #d9d9d9;
  border: 7px solid #FFFFFF;
  border-radius: 50%;
}
.program-selection__radio:hover {
  box-shadow: 0 0 0 1px #bababa;
}
@media (min-width: 768px) {
  .program-selection__radio {
    width: 18px;
    height: 18px;
    border-width: 11px;
    margin-left: -60px;
  }
}
.program-selection__input:checked + .program-selection__radio {
  background-color: #68B738;
}
.contacts {
  margin: 0;
  padding: 0;
  border: none;
  position: relative;
}
.contacts::before {
  content: "";
  position: absolute;
  top: 45px;
  right: 0;
  width: 90%;
  height: 2px;
  background-color: #68B738;
  z-index: -1;
}
@media (min-width: 768px) {
  .contacts::before {
    width: 100%;
    top: 95px;
  }
}
@media (min-width: 1400px) {
  .contacts__wrapper {
    padding: 0 70px;
  }
}
.contacts__title {
  display: block;
  font-size: 24px;
  line-height: 26px;
  margin-top: 26px;
  margin-bottom: 35px;
  background-color: #FFFFFF;
  width: calc(187 / 280 * 100%);
  padding-right: 14px;
}
@media (min-width: 768px) {
  .contacts__title {
    font-size: 36px;
    line-height: 36px;
    margin-top: 71px;
    margin-bottom: 76px;
    width: 512px;
    padding: 0 40px;
  }
}
@media (min-width: 768px) {
  .contacts__container {
    padding: 0 40px;
  }
}
@media (min-width: 1400px) {
  .contacts__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
.contacts__wrap {
  margin: 0;
  margin-bottom: 14px;
  position: relative;
}
@media (min-width: 1400px) {
  .contacts__wrap:last-child {
    margin-left: 78px;
  }
}
.contacts__caption {
  display: flex;
  align-items: baseline;
  width: 100%;
}
.contacts__input {
  height: 43px;
  flex-grow: 1;
}
@media (min-width: 768px) {
  .contacts__input {
    font-size: 20px;
    line-height: 30px;
    height: 50px;
  }
}
.contacts__input[type="email"] {
  padding-left: 15px;
  margin-left: 11px;
}
@media (min-width: 768px) {
  .contacts__input[type="email"] {
    padding-left: 18px;
    margin-left: 20px;
  }
}
.contacts__input[type="tel"] {
  padding-left: 15px;
  margin-left: 5px;
}
@media (min-width: 768px) {
  .contacts__input[type="tel"] {
    padding-left: 18px;
    margin-left: 15px;
  }
}
.contacts__icon-email,
.contacts__icon-phone {
  position: absolute;
}
.contacts__icon-email {
  top: 16px;
  right: 16px;
  stroke: #222222;
}
@media (min-width: 768px) {
  .contacts__icon-email {
    top: 19px;
    right: 19px;
  }
}
.contacts__icon-phone {
  top: 13px;
  right: 13px;
  fill: #222222;
}
@media (min-width: 768px) {
  .contacts__icon-phone {
    top: 16px;
    right: 17px;
  }
}
.input:focus + .contacts__icon-email {
  stroke: #68b738;
}
.input:focus + .contacts__icon-phone {
  fill: #68b738;
}
.input--erorr + .contacts__icon-email {
  stroke: #ff8282;
}
.input--erorr + .contacts__icon-phone {
  fill: #ff8282;
}
.comment {
  margin: 0;
  padding: 0;
  border: none;
  position: relative;
}
.comment::before {
  content: "";
  position: absolute;
  top: 40px;
  right: 0;
  width: 90%;
  height: 2px;
  background-color: #68B738;
  z-index: -1;
}
@media (min-width: 768px) {
  .comment::before {
    width: 100%;
    top: 75px;
  }
}
.comment__wrapper {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1400px) {
  .comment__wrapper {
    padding: 0 70px;
  }
}
.comment__title {
  display: block;
  margin-top: 24px;
  margin-bottom: 38px;
  font-size: 24px;
  line-height: 26px;
  background-color: #FFFFFF;
  width: calc(121 / 280 * 100%);
  padding-right: 14px;
}
@media (min-width: 768px) {
  .comment__title {
    margin-top: 50px;
    margin-bottom: 76px;
    font-size: 36px;
    line-height: 36px;
    width: 182px;
    padding: 0 40px;
  }
}
.comment__textarea {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  border: 1px solid #d9d9d9;
  color: #444444;
  padding: 0;
  padding-top: 10px;
  padding-left: 15px;
  resize: none;
  height: 138px;
  flex-grow: 1;
}
@media (min-width: 768px) {
  .comment__textarea {
    height: 174px;
    padding-left: 19px;
    font-size: 20px;
    line-height: 30px;
    margin: 0 40px;
  }
}
.additionally {
  padding: 0;
  margin: 0;
  border: none;
  position: relative;
}
.additionally::before {
  content: "";
  position: absolute;
  top: 40px;
  right: 0;
  width: 90%;
  height: 2px;
  background-color: #68B738;
  z-index: -1;
}
@media (min-width: 768px) {
  .additionally::before {
    width: 100%;
    top: 90px;
  }
}
@media (min-width: 1400px) {
  .additionally__wrapper {
    padding: 0 70px;
  }
}
.additionally__title {
  display: block;
  margin-top: 23px;
  margin-bottom: 34px;
  font-size: 24px;
  line-height: 26px;
  background-color: #FFFFFF;
  width: calc(141 / 280 * 100%);
  padding-right: 14px;
}
@media (min-width: 768px) {
  .additionally__title {
    font-size: 36px;
    line-height: 36px;
    margin-top: 65px;
    margin-bottom: 62px;
    width: 211px;
    padding: 0 40px;
  }
}
.additionally__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
}
@media (min-width: 768px) {
  .additionally__list {
    grid-template-columns: 1fr 1fr;
    padding: 0 40px;
  }
}
@media (min-width: 1400px) {
  .additionally__list {
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 1fr;
  }
}
.additionally__item {
  margin-bottom: 18px;
}
@media (min-width: 768px) {
  .additionally__item {
    margin-bottom: 45px;
  }
}
@media (min-width: 1400px) {
  .additionally__item {
    margin-bottom: 61px;
  }
}
.additionally__caption {
  padding-left: 45px;
}
.additionally__caption--disabled {
  opacity: 0.3;
}
.additionally__caption--disabled .additionally__checkbox {
  border-color: #bababa;
}
@media (min-width: 768px) {
  .additionally__caption {
    padding-left: 59px;
  }
}
.additionally__input {
  appearance: none;
}
.additionally__checkbox {
  position: absolute;
  width: 25px;
  height: 25px;
  margin-left: -45px;
  background-color: #FFFFFF;
  border: 1px solid #d9d9d9;
}
.additionally__checkbox:hover {
  border-color: #bababa;
}
@media (min-width: 768px) {
  .additionally__checkbox {
    margin-left: -59px;
    margin-top: -5px;
    width: 38px;
    height: 38px;
  }
}
.additionally__input:checked + .additionally__checkbox {
  background-image: url("../img/Vector.svg");
  background-position: 50% 50%;
  background-repeat: no-repeat;
}
